import React, { useRef, useState } from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { postFromTrail, postTrek } from '../../../api/trail';
import { getAlert } from '../../../redux/features/alertMessageSlice';
import ImagesURLBox from '../../ImagesURLBox/ImagesURLBox';
import ImageURLBox from '../../ImageURLBox/ImageURLBox';
import ButtonSendBox from '../ButtonSendBox/ButtonSendBox';

import InputBox from '../InputBox/InputBox';
import InputFilesURLBox from '../InputFilesURLBox/InputFilesURLBox';
import InputFileTrekBox from '../InputFileTrekBox/InputFileTrekBox';
import InputFileURLBox from '../InputFileURLBox/InputFileURLBox';
import SelectBox from '../SelectBox/SelectBox';
import TextArea from '../TextArea/TextArea';

import classes from './FormTrailEdit.module.css';
import { createFormData, resetForm, validate } from './service';

const FormTrailEdit = ({ trail, type }) => {
	const [form, setForm] = useState(() => {
		if (!trail) return resetForm;
		const newTrail = { ...trail, cardPhoto: { source: trail.cardPhoto } };
		const descPhotos = trail.descPhoto.map((photo, index) => ({
			source: photo,
			name: `name-${index}`,
		}));
		return { ...newTrail, descPhoto: trail.descPhotos, descPhotos };
	});

	const fileTrek = useRef(trail ? { old: trail.fileTrekName } : '');

	const dispatch = useDispatch();
	const navigate = useNavigate();

	const sendForm = event => {
		event.preventDefault();

		const isValidate = validate(form);
		if (!isValidate)
			return dispatch(
				getAlert({ message: '   !', type: 'warning', isOpened: true })
			);

		if (!fileTrek.old) {
			const formData = createFormData(fileTrek.current.source);
			postTrek(formData).catch(error => console.log(error));
		}

		postFromTrail(form, type)
			.then(data => {
				if (data?.status === 200) {
					dispatch(
						getAlert({ message: '   !', type: 'success', isOpened: true })
					);
				}
				navigate(`/trails/${data.data.trailId}`);
			})
			.finally(() => {
				fileTrek.current = '';
				setForm(resetForm);
			});
	};

	return (
		<form className={classes.form}>
			<div className={classes.block}>
				<SelectBox
					form={form}
					setForm={setForm}
					keyObject="state"
					title=" :"
					values={[
						{ id: 0, name: '' },
						{ id: 1, name: '' },
						{ id: 2, name: '-' },
						{ id: 3, name: '-' },
						{ id: 4, name: ' ' },
						{ id: 5, name: '' },
					]}
					boxStyle={{ marginRight: '15px' }}
				/>
				<SelectBox
					form={form}
					setForm={setForm}
					keyObject="bikeType"
					title=" :"
					values={[
						{ id: 0, name: '' },
						{ id: 1, name: '' },
						{ id: 2, name: '' },
					]}
					boxStyle={{ marginRight: '15px' }}
				/>
				<InputBox
					form={form}
					setForm={setForm}
					keyObject="nameRoute"
					title=" :"
					type="text"
					boxStyle={{ marginRight: '15px' }}
				/>
				<InputBox
					form={form}
					setForm={setForm}
					keyObject="start"
					title="  :"
					type="text"
					boxStyle={{ marginRight: '15px' }}
				/>
				<InputBox
					form={form}
					setForm={setForm}
					keyObject="turn"
					title=" :"
					type="text"
					boxStyle={{ marginRight: '15px' }}
				/>
				<InputBox
					form={form}
					setForm={setForm}
					keyObject="finish"
					title="  :"
					type="text"
					boxStyle={{ marginRight: '15px' }}
				/>
				<InputBox
					form={form}
					setForm={setForm}
					keyObject="distance"
					title="   :"
					type="number"
					boxStyle={{ marginRight: '15px' }}
				/>
				<InputBox
					form={form}
					setForm={setForm}
					keyObject="ascent"
					title="     :"
					type="number"
					boxStyle={{ marginRight: '15px' }}
				/>
				<InputBox
					form={form}
					setForm={setForm}
					keyObject="urlTrekGConnect"
					title="    GConnect:"
					type="text"
					boxStyle={{ marginRight: '15px' }}
				/>
				<InputBox
					form={form}
					setForm={setForm}
					keyObject="urlVideo"
					title="    :"
					type="text"
					boxStyle={{ marginRight: '15px' }}
				/>
				<InputFileTrekBox
					fileRef={fileTrek}
					keyObject="fileTrekName"
					setForm={setForm}
					title="   FIT/GPX/TCX:"
					boxStyle={{ marginRight: '15px' }}
				/>
				<TextArea
					form={form}
					setForm={setForm}
					title="   . , , ,    ."
					keyObject="descriptionArea"
					boxStyle={{ marginRight: '15px' }}
				/>
			</div>

			<div className={classes.block}>
				<InputFileURLBox
					form={form}
					setForm={setForm}
					title="  :"
					keyObject="cardPhoto"
					boxStyle={{ marginRight: '15px' }}
				/>
				<ImageURLBox form={form} setForm={setForm} keyObject="cardPhoto" />
			</div>
			<InputFilesURLBox
				form={form}
				setForm={setForm}
				title="  :"
				boxStyle={{ marginRight: '15px' }}
			/>
			<ImagesURLBox form={form} setForm={setForm} boxStyle={{ marginRight: '15px' }} />
			<ButtonSendBox sendForm={sendForm} title="   !" />
		</form>
	);
};

export default FormTrailEdit;
