export function validateUsername(register) {
	return {
		...register('username', {
			required: '    ',
			minLength: { value: 5, message: 'Username    4 ' },
			maxLength: { value: 15, message: 'Username     15 ' },
		}),
	};
}

export function validateEmail(register) {
	return {
		...register('email', {
			required: '    ',
			pattern: {
				value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
				message: ' email',
			},
		}),
	};
}

export function validatePassword(register) {
	return {
		...register('password', {
			required: '    ',
			minLength: { value: 6, message: 'Password    5 ' },
			maxLength: { value: 15, message: 'Password     15 ' },
		}),
	};
}
